.btn {
	position: relative;
	overflow: hidden;
	display: inline-block;
	vertical-align: top;
	padding: 0 15px;
	min-width: 120px;
	border: none;
	outline: none;
	color: $white;
	background: $blue;
	@include f(19px, 300, 60px);
	text-align: center;
	text-decoration: none;
	cursor: pointer;
	transition: opacity .175s, background .175s, box-shadow .175s;
	@include boost {
		background: $elevatePrimary; }
	@include metro {
		background: $metroSecondary; }
	&:active {
		color: $white;
		background: darken($blue, 10%);
		box-shadow: inset 2px 2px 6px rgba(0,0,0,.2);
		@include boost {
			background: darken($elevatePrimary, 10%); }
		@include metro {
			background: darken($metroSecondary, 10%); } }
	&:hover {
		.icon-right {
			transform: translate3d(50%,0,0); }
		.icon-left {
			transform: translate3d(-50%,0,0); } }
	&_gray {
		background: #bec1cb;
		@include boost {
			background: #bec1cb; }
		@include metro {
			background: #bec1cb; }
		&:active {
			background: darken(#bec1cb, 10%);
			@include boost {
				background: darken(#bec1cb, 10%); }
			@include metro {
				background: darken(#bec1cb, 10%); } } }
	&_red {
		background: $red;
		@include boost {
			background: $red; }
		&:active {
			background: darken($red, 10%);
			@include boost {
				background: darken($red, 10%); }
			@include metro {
				background: darken($red, 10%); } } }
	&_wide {
		width: 100%; }
	&_sm {
		height: 44px;
		line-height: 44px; }
	.icon-right,
	.icon-left {
		display: inline-block;
		vertical-align: top;
		margin-top: 22px;
		fill: currentColor;
		font-size: 80%;
		transition: transform .175s; } }

.btn-toggle {
	width: 40px;
	height: 40px;
	background: none;
	@include vertical;
	@include hover {
		.btn-toggle {
			&__visible,
			&__hidden {
				fill: $blue; } } }
	@include hover('boost') {
		.btn-toggle {
			&__visible,
			&__hidden {
				fill: $elevatePrimary; } } }
	@include hover('metro') {
		.btn-toggle {
			&__visible,
			&__hidden {
				fill: $metroSecondary; } } }
	&.is-active {
		.btn-toggle {
			&__visible {
				display: none; }
			&__hidden {
				display: inline-block; } } }
	&__visible,
	&__hidden {
		display: inline-block;
		vertical-align: middle;
		fill: $black;
		font-size: (20px*0.75);
		transition: fill 0.25s; }
	&__hidden {
		display: none; } }
