.textarea {
	padding: 20px 15px;
	border: 1px solid #ccc;
	border-radius: 0;
	width: 100%;
	height: 200px;
	background: $white;
	box-shadow: none;
	@include f(16px, 400, normal);
	appearance: none;
	resize: none;
	transition: border-color 0.25s;
	@include sm {
		height: 50px; }
	&::-ms-clear {
		display: none; }
	&:focus {
		border-color: $blue;
		@include boost {
			border-color: $elevateSecondary; }
		@include metro {
			border-color: $metroSecondary; } }
	&:disabled {
		border-color: lighten($darkGray, 50%);
		background: lighten($darkGray, 75%);
		cursor: not-allowed; }
	&.is-error {
		border-color: $red; }
	&_sm {
		padding: 10px 15px;
		height: 150px; } }
